.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 220);
  border-radius: 10px;
  overflow: hidden;
}
.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 220);
  display: flex;
  align-items: center;
}
.p-circle {
  background-color: white;
  border-radius: 50%;
  height: 10px;
  width: 10px;
  margin: 4px;
}
